import { Injectable } from '@angular/core';
import { Router, UrlTree } from '@angular/router';
import { APP_ROUTE } from '@core/constants';
import { AuthService } from '@core/data-access/services/auth.service';
import { Observable } from 'rxjs';

@Injectable({
	providedIn: 'root',
})
export class AgentGuard {
	constructor(
		private readonly auth: AuthService,
		private readonly router: Router
	) {}

	canActivate(): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
		const IS_AGENT = this.auth.user()?.roles.some((role) => role === 'isAgentUser');

		if (!IS_AGENT) {
			return true;
		}

		this.router.navigate([`${APP_ROUTE.myProfile}/${APP_ROUTE.businessProfile.base}`]);
		return false;
	}
}
