import { Component, Input } from '@angular/core';
import { APP_ROUTE } from '@core/constants';
import { OFFCANVAS_HEADER_SIDENAV } from '@core/constants/offcanvas.constant';
import { RouteDataType, RouteService } from '@core/services/route.service';
import { NgbOffcanvas } from '@ng-bootstrap/ng-bootstrap';
import { SharedModule } from '@shared/shared.module';

import { MatTabsModule } from '@angular/material/tabs';
import { IntercomService } from '@core/services/intercom.service';
import { HeaderAccountMenuModule } from '@core/components/header/components/header-account-menu/header-account-menu.module';
import { HeaderSidenavComponent } from '@core/components/header/components/header-sidenav/header-sidenav.component';
import { Router } from '@angular/router';

interface NavItem {
	label: string;
	icon: string;
	route: string;
	queryParams?: { sellerTypeId: number };
}

@Component({
    imports: [SharedModule, MatTabsModule, HeaderAccountMenuModule],
    selector: 'findqo-bottom-navbar',
    templateUrl: './bottom-navbar.component.html',
    styleUrls: ['./bottom-navbar.component.scss']
})
export class BottomNavbarComponent {
	constructor(
		private ngbOffCanvas: NgbOffcanvas,
		public routeService: RouteService,
		private intercomService: IntercomService,
		private router: Router
	) {}

	@Input() isAgentHub = false;
	readonly routeDataType = RouteDataType;
	selectedRoute: string | null = null;

	navItems: NavItem[] = [
		{
			label: 'Listings',
			icon: 'summarize',
			route: `${APP_ROUTE.agentHub.listings}`,
		},
		{
			label: 'Enquiries',
			icon: 'mail',
			route: `${APP_ROUTE.agentHub.enquiries}`,
		},
		{
			label: 'List Property',
			icon: 'add_circle',
			route: `/${APP_ROUTE.placeAdCategory}`,
			queryParams: { sellerTypeId: 2 },
		},
		{
			label: 'Agents',
			icon: 'person',
			route: `${APP_ROUTE.agentHub.agents}`,
		},
	];

	onOpenHeaderSidenav(): void {
		this.intercomService.toggelIntercomPopup(true);
		const OPENED_REF = this.ngbOffCanvas.open(HeaderSidenavComponent, OFFCANVAS_HEADER_SIDENAV);
		OPENED_REF.componentInstance.isAgentHub = true;
	}

	ngOnInit(): void {
		this.router.events.subscribe(() => {
			this.selectedRoute = this.router.url;
		});
	}
}
