import { NgModule } from '@angular/core';
import { Route, RouterModule } from '@angular/router';
import { AgentHubComponent } from './agent-hub.component';
import { APP_ROUTE } from '@core/constants';
import { SeoGuard } from '@core/guards/seo/seo.guard';
import { AuthGuard } from '@core/guards/auth.guard';
import { RobotsGuard } from '@core/guards/seo/robots.guard';
import { BussinessProfileGuard } from '@core/guards/business-profile.guard';
import { RouteDataType } from '@core/services/route.service';
import { AgentGuard } from '@core/guards/agent.guard';

const routes: Route[] = [
	{
		path: '',
		component: AgentHubComponent,
		children: [
			{
				path: '',
				redirectTo: APP_ROUTE.agentHub.listings,
				pathMatch: 'full',
			},
			{
				path: APP_ROUTE.agentHub.listings,
				loadChildren: () => import('./pages/listings/listings.module').then((m) => m.ListingsModule),
			},
			{
				path: APP_ROUTE.agentHub.enquiries,
				loadChildren: () => import('./pages/enquiries/enquiries.module').then((m) => m.EnquiriesModule),
			},
			{
				path: APP_ROUTE.agentHub.agents,
				loadChildren: () => import('./pages/agents/agents.module').then((m) => m.AgentsModule),
			},
			{
				path: APP_ROUTE.businessProfile.agentList,
				redirectTo: APP_ROUTE.agentHub.agents,
				pathMatch: 'full',
			},
			{
				path: APP_ROUTE.agentHub.adDetail,
				data: { title: 'n476' },
				loadChildren: () => import('./pages/ad-detail/ad-detail.module').then((m) => m.AdDetailPageModule),
				canActivate: [SeoGuard],
			},
			{
				path: APP_ROUTE.agentHub.enquiryDetail,
				data: { title: 'n4074' },
				loadChildren: () => import('./pages/enquiry-detail/enquiry-detail.module').then((m) => m.EnquiryDetailPageModule),
				canActivate: [SeoGuard],
			},
			{
				path: APP_ROUTE.meetingSchedule.base,
				data: { title: 'n3917', [RouteDataType.IS_HEADER_VISIBLE]: false, [RouteDataType.IS_FOOTER_VISIBLE]: false },
				loadChildren: () => import('@feature/meeting-schedule/meeting-schedule.module').then((m) => m.MeetingScheduleModule),
				canActivate: [AuthGuard, SeoGuard, RobotsGuard],
			},
			{
				path: APP_ROUTE.myProfile,
				redirectTo: '/' + APP_ROUTE.myProfile,
				pathMatch: 'full',
			},
			{
				path: APP_ROUTE.businessProfile.base,
				loadComponent: () => import('@feature/my-profile/pages/business-profile/business-profile.page').then((c) => c.BusinessProfilePage),
				data: { title: 'n4042' },
				canActivate: [SeoGuard, BussinessProfileGuard],
			},
			{
				path: APP_ROUTE.businessProfile.edit,
				loadChildren: () => import('@feature/my-profile/v2/components/business-account-v2/business-account-v2.module').then((c) => c.BusinessAccountV2Module),
				canActivate: [BussinessProfileGuard, AgentGuard],
			},
			{
				path: APP_ROUTE.businessProfile.subscription,
				loadComponent: () => import('@feature/my-profile/pages/subscription-plan/subscription-plan.component').then((c) => c.SubscriptionPlanComponent),
				data: { title: 'n4032' },
				canActivate: [SeoGuard, BussinessProfileGuard, AgentGuard],
			},
			{
				path: APP_ROUTE.businessProfile.paymentPlan,
				loadComponent: () => import('@feature/my-profile/pages/payment-plan/payment-plan.component').then((c) => c.PaymentPlanComponent),
				data: { title: 'n4032' },
				canActivate: [SeoGuard, BussinessProfileGuard, AgentGuard],
			},
			{
				path: APP_ROUTE.businessProfile.paymentSuccess,
				loadComponent: () => import('@feature/my-profile/pages/payment-sucess/payment-sucess.page').then((c) => c.PaymentSucessPage),
			},
			{
				path: APP_ROUTE.businessProfile.welcome,
				loadComponent: () => import('@feature/my-profile/pages/welcome-business/welcome-business.page').then((c) => c.WelcomeBusinessPage),
			},
			{
				path: APP_ROUTE.myAdmatchingTenants,
				loadChildren: () =>
					import('@feature/my-ads/pages/my-ad-matching-tenant-list/my-ad-matching-tenant-list.module').then((m) => m.MyAdMatchingTenantListPageModule),
				canActivate: [AuthGuard, SeoGuard],
			},

			{
				path: APP_ROUTE.agentHub.leads,
				loadChildren: () => import('./pages/leads/leads.module').then((m) => m.LeadsModule),
			},
		],
	},
];

@NgModule({
	imports: [RouterModule, RouterModule.forChild(routes)],
	exports: [RouterModule],
})
export class AgentHubRoutingModule {}
